.about {
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;

    width: 100%;
    min-width: 1200px;
    min-height: 610px;
    overflow-y: hidden;

    & > * { user-select: none }


    .message__next{
        padding-left: 14px;
        padding-right: 14px;
    }
}

.about__astronaut {
    position: absolute;
    left: 48%;
    bottom: 0;
    transform: translateX(-50%);
    z-index: 110;

    user-select: none;

    @media (max-height: 840px), (max-width: 1480px) { max-width: 350px }
}

.about__messages {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 591px;
    margin-left: -268px;


    z-index: 100;

    @media (max-height: 840px), (max-width: 1480px) {
        max-width: 350px;
        bottom: 452px;
        margin-left: -233px;
    }
}

.message {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    transform: translateX(-240px);
    font-size: 16px;

    @media (max-height: 840px), (max-width: 1480px) { font-size: 16px }
}

.about__message {
    transform: translateY(200px);
    opacity: 0;
    transition: .8s;
    position: absolute;
    left: 0;
    top: 0;
    &.is-active {
        transform: translateY(0);
        opacity: 1;
        z-index: 50;
    }
    &.is-no-active { transform: translateY(-200px) }

    @media (max-height: 840px), (max-width: 1480px) {
        .btn.--link {
            font-size: 14px;
        }
    }
}

.message__next {
    padding-left: 24px;
    padding-right: 24px;
    height: 48px;
    position: relative;
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    user-select: none;

    &.message__next--autoheight {
        flex-wrap: wrap;
        max-width: 216px;
        height: 68px;
        padding-left: 24px;
        width: 100%;
        padding-right: 24px;
    }
    &:after {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: #FFF0CB;
        box-shadow: 0px 12px 40px #FFCC55, inset 0px -4px 8px rgba(225, 162, 14, 0.4);
        border-radius: 8px;
        transform: skewX(-8deg);
        z-index: 1;

        transition: .4s;
    }
    .btn {
        position: relative;
        z-index: 20;
        transform: translateY(-3px);

        font-size: 14px;
        transition: .4s;
        line-height: 140%;
    }
    
    &:hover {
        .btn { border-bottom-color: #dbbcff; }
    }
}

